/* add css module styles here (optional) */
.dtWrapper {
  margin: auto;
  width: 21.25rem;
  position: relative;
  padding: 0.5rem;
  border-radius: 0.25rem; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header .header--btn {
    cursor: pointer;
    display: block;
    width: 1.5rem;
    height: 1.5rem; }
    .header .header--btn.is-disabled {
      opacity: 0.2;
      pointer-events: none; }

.yearWrapper {
  position: relative; }

.yearWrapper:after,
.yearWrapper:before {
  content: '';
  display: block;
  width: 100%;
  height: 1.875rem;
  position: absolute;
  left: 0;
  transition: 0.4s;
  transition-delay: 0.2s; }

.yearWrapper:before {
  background-image: linear-gradient(0deg, #fff, #fff 10%, rgba(245, 245, 245, 0));
  bottom: 0; }

.yearWrapper:after {
  background-image: linear-gradient(180deg, #fff, #fff 10%, rgba(245, 245, 245, 0));
  top: -0.1em; }

.yearList {
  list-style: none;
  overflow: hidden;
  padding: 0;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  height: 18.1875rem;
  overflow-y: scroll;
  padding: 1.875rem 0;
  margin: 0.5rem 0; }

.yearList::-webkit-scrollbar {
  display: none; }

.yearList_year {
  width: 20%;
  height: 3.4375rem;
  line-height: 3.4375rem;
  float: left;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer; }

.yearList_year:nth-child(5n + 1) {
  clear: left; }

.monthList {
  list-style: none;
  overflow: hidden;
  padding: 0;
  text-align: center; }

.monthList_month {
  width: calc(340px / 3);
  height: 4.5rem;
  line-height: 4.5rem;
  float: left;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer; }
  .monthList_month.is-disabled {
    opacity: 0.2;
    pointer-events: none; }

.monthList_month:nth-child(3n + 1) {
  clear: left; }

.is-rtl.monthList .monthList_month {
  float: right; }

.is-rtl.monthList .monthList_month:nth-child(3n + 1) {
  clear: right; }

.daysList {
  list-style: none;
  overflow: hidden;
  padding: 0;
  text-align: center; }
  .daysList .daysList-day {
    width: calc(340px / 7);
    height: calc(340px / 7);
    line-height: calc(340px / 7);
    float: left;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer; }
  .daysList .daysList-day:nth-child(7n + 1) {
    clear: left; }
  .daysList .is-today {
    border: 0.125rem solid green; }
  .daysList .is-disabled {
    pointer-events: none;
    background-color: #aaa;
    opacity: 0.7; }
  .daysList .is-disabled-by-user {
    pointer-events: none;
    background-color: #ccc;
    opacity: 0.3; }
  .daysList .is-minMaxDisabled {
    pointer-events: none;
    background-color: #ccc;
    opacity: 0.3; }
  .daysList .is-selected-day {
    background-color: yellow; }
  .daysList .is-selected-day-from {
    background-color: cyan;
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%; }
  .daysList .is-selected-day-to {
    background-color: yellowgreen;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%; }
  .daysList .is-selected-day-range {
    background-color: yellow; }
  .daysList .is-pointer {
    cursor: pointer; }
  .daysList .is-weekends {
    color: red; }

.is-rtl.daysList .daysList-day {
  float: right; }

.is-rtl.daysList .daysList-day:nth-child(7n + 1) {
  clear: right; }

.is-rtl.daysList .is-selected-day-from {
  background-color: cyan;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%; }

.is-rtl.daysList .is-selected-day-to {
  background-color: yellowgreen;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%; }

.calender-modal {
  position: absolute;
  background-color: #fff;
  padding: 0.375rem 0;
  z-index: 5000;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.25); }

.input-picker {
  position: relative;
  display: inline-block; }
  .input-picker .input-picker--input {
    height: 2rem;
    line-height: 2rem;
    padding: 0 8px; }
  .input-picker .input-picker--clearBtn {
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    position: absolute;
    top: 50%;
    right: 0.3125rem;
    transform: translateY(-50%);
    cursor: pointer;
    color: #ccc; }

.todayBtn {
  display: block;
  text-align: center;
  margin: auto; }
